// app/servicos/automacao-whatsapp/page.tsx

'use client';

import { useState } from 'react';

export default function AutomacaoWhatsapp() {
  // Para futura integração: status de envio do cadastro
  const [enviado, setEnviado] = useState(false);

  function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setEnviado(true);
    // Aqui depois colocaremos integração com banco e gateway de pagamento
  }

  return (
    <div className="min-h-screen bg-slate-50 py-12 px-4">
      <div className="max-w-3xl mx-auto bg-white shadow-xl rounded-2xl p-10">
        {/* Título & vídeo */}
        <h1 className="text-3xl font-bold text-blue-700 mb-4">Automação de WhatsApp e Atendimento Digital</h1>
        <p className="text-gray-600 mb-6">
          Impulsione seu atendimento, aumente vendas e reduza tempo com nossa automação exclusiva para WhatsApp. Chatbots inteligentes, integrações e métricas em tempo real!
        </p>
        
        {/* (Coloque seu vídeo aqui) */}
        <div className="mb-8">
          <iframe
            width="100%"
            height="315"
            src="https://www.youtube.com/embed/SEU_VIDEO_AQUI"
            title="Apresentação do serviço"
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          ></iframe>
        </div>
        
        {/* Explicações */}
        <div className="mb-8">
          <h2 className="text-xl font-bold mb-2">Por que automatizar seu WhatsApp?</h2>
          <ul className="list-disc pl-6 text-gray-700 space-y-1">
            <li>Respostas instantâneas 24/7</li>
            <li>Captação automática de leads</li>
            <li>Relatórios e métricas em tempo real</li>
            <li>Integração com CRM e outros sistemas</li>
            <li>Atendimento escalável sem aumentar custos</li>
          </ul>
        </div>

        {/* Área de cadastro/pagamento */}
        <div className="bg-blue-50 rounded-xl p-6 mt-8">
          <h3 className="text-lg font-semibold mb-2 text-blue-700">Comece agora por apenas <span className="text-2xl">R$ 150/mês</span></h3>
          
          {!enviado ? (
            <form className="flex flex-col gap-4" onSubmit={handleSubmit}>
              <input
                type="text"
                placeholder="Seu nome"
                className="border rounded p-3"
                required
              />
              <input
                type="email"
                placeholder="Seu e-mail"
                className="border rounded p-3"
                required
              />
              <input
                type="text"
                placeholder="WhatsApp"
                className="border rounded p-3"
                required
              />
              {/* Aqui depois integramos cartão/pagamento */}
              <button className="bg-blue-700 text-white font-bold py-3 rounded hover:bg-blue-800 transition">Quero contratar agora</button>
            </form>
          ) : (
            <div className="text-green-600 font-bold mt-4">
              Cadastro enviado! Entraremos em contato para ativação do serviço e pagamento.
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
